import { renderUserBoxs, renderUserBoxsSmScreens } from './data';


export default function Equipo() {
    return (
        <section className='py-10 bg-white font-sans relative'>

            <img src={require('./images/bg.png')} className='absolute w-full h-full object-cover right-0 top-0' />

            <div className='container flex flex-col items-center justify-center !my-32'>

                <div className='hidden lg:block h-[600px] w-[600px] rounded-[200px] border border-[#45B8EA] relative'>

                    {renderUserBoxs}

                </div>

                <div className='lg:hidden flex items-center justify-center flex-col'>

                    <h1 className='font-medium mb-10'>Equipo</h1>

                    {renderUserBoxsSmScreens}

                </div>

            </div>
        </section>
    )
}
